// Checkbox.tsx
import clsx from 'clsx';
import { focusRing } from './../../../tailwind/styles/focus';
import { DefaultIcon } from '../icon/Icon';
import { CheckboxProps, CheckboxScope } from './checkbox.interface';
import { borderRadius } from './../../../tailwind/styles/border-radius';

export const Checkbox: React.FC<CheckboxProps> = ({ checked, disabled, error, label, onChange }) => {
  return (
    <label
      className={clsx(
        CheckboxScope,
        'rounded',
        'inline-flex',
        'flex-row',
        'flex-wrap',
        'items-center',
        'justify-start',
        'relative',
        'select-none',
        [disabled ? 'cursor-not-allowed opacity-zep-40' : 'cursor-pointer [&>div]:hover:border-zep-grey-900'],
        { ' [&>div]:hover:border-zep-red-500': error },
      )}
    >
      <input type="checkbox" checked={checked} onChange={onChange} className="sr-only" disabled={disabled} />
      <div
        tabIndex={0}
        className={clsx(
          borderRadius,
          focusRing,
          'focus-visible:ring-offset-1',
          'w-zep-1_5',
          'h-zep-1_5',
          'rounded',
          'border',
          'border-zep-grey-900',
          [checked ? 'bg-zep-grey-900 border-zep-grey-900' : 'bg-white'],
          { 'bg-zep-red-500 border-zep-red-500 ': error && checked },
          { 'border-zep-red-500': error && !checked },
        )}
      >
        {checked && (
          <DefaultIcon
            name="Check"
            className={clsx('w-zep-1_5 h-zep-1_5 text-white absolute top-[-0.1px] left-[-0.1px]')}
          />
        )}
      </div>
      <span className="ml-zep-0_75">{label}</span>
    </label>
  );
};
